 /**
  * Clamps (or clips or confines) the value to be between min and max.
  * @param {number} value
  * @param {number} min
  * @param {number} max
  * @return {number}
  */
function clamp(value, min, max) {
  if (value < min) {
    return min;
  }
  if (value > max) {
    return max;
  }
  return value;
}

module.exports = clamp;



