	module.exports = "<!-- File generated from \"src/FixedDataTableColumnNew.react.js\" -->\n<h1 id=\"-column-component-\"><code>Column</code> (component)</h1>\n<p>Component that defines the attributes of table column.</p>\n<h2 id=\"props\">Props</h2>\n<h3 id=\"-align-\"><code>align</code></h3>\n<p>The horizontal alignment of the table cell content.</p>\n<p>type: <code>enum(&#39;left&#39;|&#39;center&#39;|&#39;right&#39;)</code></p>\n<h3 id=\"-fixed-\"><code>fixed</code></h3>\n<p>Controls if the column is fixed when scrolling in the X axis.</p>\n<p>type: <code>bool</code>\ndefaultValue: <code>false</code></p>\n<h3 id=\"-header-\"><code>header</code></h3>\n<p>The header cell for this column.\nThis can either be a string a React element, or a function that generates\na React Element. Passing in a string will render a default header cell\nwith that string. By default, the React element passed in can expect to\nreceive the following props:</p>\n<code class=\"codeBlock\">props<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n  columnKey<span class=\"token punctuation\" >:</span> string <span class=\"token comment\" spellcheck=\"true\">// (of the column, if given)\n</span>  height<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Table or rowHeightGetter)\n</span>  width<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Column)\n</span><span class=\"token punctuation\" >}</span></code><p>Because you are passing in your own React element, you can feel free to\npass in whatever props you may want or need.</p>\n<p>If you pass in a function, you will receive the same props object as the\nfirst argument.</p>\n<p>type: <code>union(node|func)</code></p>\n<h3 id=\"-cell-\"><code>cell</code></h3>\n<p>This is the body cell that will be cloned for this column.\nThis can either be a string a React element, or a function that generates\na React Element. Passing in a string will render a default header cell\nwith that string. By default, the React element passed in can expect to\nreceive the following props:</p>\n<code class=\"codeBlock\">props<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n  rowIndex<span class=\"token punctuation\" >;</span> number <span class=\"token comment\" spellcheck=\"true\">// (the row index of the cell)\n</span>  columnKey<span class=\"token punctuation\" >:</span> string <span class=\"token comment\" spellcheck=\"true\">// (of the column, if given)\n</span>  height<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Table or rowHeightGetter)\n</span>  width<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Column)\n</span><span class=\"token punctuation\" >}</span></code><p>Because you are passing in your own React element, you can feel free to\npass in whatever props you may want or need.</p>\n<p>If you pass in a function, you will receive the same props object as the\nfirst argument.</p>\n<p>type: <code>union(node|func)</code></p>\n<h3 id=\"-footer-\"><code>footer</code></h3>\n<p>This is the footer cell for this column.\nThis can either be a string a React element, or a function that generates\na React Element. Passing in a string will render a default header cell\nwith that string. By default, the React element passed in can expect to\nreceive the following props:</p>\n<code class=\"codeBlock\">props<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n  columnKey<span class=\"token punctuation\" >:</span> string <span class=\"token comment\" spellcheck=\"true\">// (of the column, if given)\n</span>  height<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Table or rowHeightGetter)\n</span>  width<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Column)\n</span><span class=\"token punctuation\" >}</span></code><p>Because you are passing in your own React element, you can feel free to\npass in whatever props you may want or need.</p>\n<p>If you pass in a function, you will receive the same props object as the\nfirst argument.</p>\n<p>type: <code>union(node|func)</code></p>\n<h3 id=\"-columnkey-\"><code>columnKey</code></h3>\n<p>This is used to uniquely identify the column, and is not required unless\nyou a resizing columns. This will be the key given in the\n<code>onColumnResizeEndCallback</code> on the Table.</p>\n<p>type: <code>union(string|number)</code></p>\n<h3 id=\"-width-required-\"><code>width</code> (required)</h3>\n<p>The pixel width of the column.</p>\n<p>type: <code>number</code></p>\n<h3 id=\"-minwidth-\"><code>minWidth</code></h3>\n<p>If this is a resizable column this is its minimum pixel width.</p>\n<p>type: <code>number</code></p>\n<h3 id=\"-maxwidth-\"><code>maxWidth</code></h3>\n<p>If this is a resizable column this is its maximum pixel width.</p>\n<p>type: <code>number</code></p>\n<h3 id=\"-flexgrow-\"><code>flexGrow</code></h3>\n<p>The grow factor relative to other columns. Same as the flex-grow API\nfrom <a href=\"http://www.w3.org/TR/css3-flexbox/\">http://www.w3.org/TR/css3-flexbox/</a>. Basically, take any available\nextra width and distribute it proportionally according to all columns&#39;\nflexGrow values. Defaults to zero (no-flexing).</p>\n<p>type: <code>number</code></p>\n<h3 id=\"-isresizable-\"><code>isResizable</code></h3>\n<p>Whether the column can be resized with the\nFixedDataTableColumnResizeHandle. Please note that if a column\nhas a flex grow, once you resize the column this will be set to 0.</p>\n<p>This property only provides the UI for the column resizing. If this\nis set to true, you will need to set the onColumnResizeEndCallback table\nproperty and render your columns appropriately.</p>\n<p>type: <code>bool</code></p>\n<h3 id=\"-allowcellsrecycling-\"><code>allowCellsRecycling</code></h3>\n<p>Whether cells in this column can be removed from document when outside\nof viewport as a result of horizontal scrolling.\nSetting this property to true allows the table to not render cells in\nparticular column that are outside of viewport for visible rows. This\nallows to create table with many columns and not have vertical scrolling\nperformance drop.\nSetting the property to false will keep previous behaviour and keep\ncell rendered if the row it belongs to is visible.</p>\n<p>type: <code>bool</code>\ndefaultValue: <code>false</code></p>\n";

