	module.exports = "<!-- File generated from \"src/FixedDataTableColumnGroupNew.react.js\" -->\n<h1 id=\"-columngroup-component-\"><code>ColumnGroup</code> (component)</h1>\n<p>Component that defines the attributes of a table column group.</p>\n<h2 id=\"props\">Props</h2>\n<h3 id=\"-align-\"><code>align</code></h3>\n<p>The horizontal alignment of the table cell content.</p>\n<p>type: <code>enum(&#39;left&#39;|&#39;center&#39;|&#39;right&#39;)</code></p>\n<h3 id=\"-fixed-\"><code>fixed</code></h3>\n<p>Controls if the column group is fixed when scrolling in the X axis.</p>\n<p>type: <code>bool</code>\ndefaultValue: <code>false</code></p>\n<h3 id=\"-header-\"><code>header</code></h3>\n<p>This is the header cell for this column group.\nThis can either be a string or a React element. Passing in a string\nwill render a default footer cell with that string. By default, the React\nelement passed in can expect to receive the following props:</p>\n<code class=\"codeBlock\">props<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n  height<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the groupHeaderHeight)\n</span>  width<span class=\"token punctuation\" >:</span> number <span class=\"token comment\" spellcheck=\"true\">// (supplied from the Column)\n</span><span class=\"token punctuation\" >}</span></code><p>Because you are passing in your own React element, you can feel free to\npass in whatever props you may want or need.</p>\n<p>You can also pass in a function that returns a react elemnt, with the\nprops object above passed in as the first parameter.</p>\n<p>type: <code>union(node|func)</code></p>\n";

